<template>
  <div class="convertRecord">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>兑换记录</span>
    </header>
    <div class="record_list" v-for="item in list">
      <section class="record_list_l">
        <img :src="item.pic_url" alt="">
      </section>
      <section class="record_list_c">
        <p>兑换商品: <span>{{item.good_name}}</span></p>
        <p>商品数量: <span>{{item.good_num}}</span></p>
        <p>商品价格: <span>{{item.good_price}}</span></p>
        <p>商品积分: <span>{{item.jifen}}</span></p>
        <p>兑换时间: <span>{{item.create_time | time}}</span></p>
        <p>订单编号: <span>{{item.order_sn}}</span></p>
      </section>
      <section class="record_list_r">
        <p>{{item.type | typeFilter}}</p>
      </section>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        list:[]
      }
    },
    filters:{
      typeFilter(val){
          if (val === 0){
            return "未发货"
          } else if (val === 1){
            return "已发货"
          } else {
            return "已收货"
          }
      }
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      record(page=1){
        this.post('home/User/getJifenDhLog',{
          page: page,
          number:20
        }).then(res => {
          if (res.data.code === 10000){
            this.list = res.data.info.info;
            this.list.forEach(item => {
              item.pic_url = this.imageURL(item.pic_url)
            })
          }
        })
      }
    },
    mounted(){
      this.record();
    }
  }
</script>
<style scoped>
  @import "../../../static/header.css";
  .convertRecord {
      height: 100vh;
      background-color: #f2f2f2;
  }
  .record_list {
    padding: 0.3rem 0.2rem;
    background-color: #ffffff;
    margin-top: 0.2rem;
    display: flex;
  }
  .record_list_l {
    width: 1.4rem;
    height: 1.4rem;
    overflow: hidden;
    position: relative;
    margin-right: 0.2rem;
  }
  .record_list_l img {
    position: absolute;
    top: 0;
    width: 100%;
    min-height: 100%;
  }
  .record_list_c{
    flex: 1;
  }
  .record_list_c p{
    color: #444444;
    font-size: 0.28rem;
    margin-bottom: 0.1rem;
  }
  .record_list_c span {
    color: #111111;
    font-size: 0.3rem;
  }
  .record_list_r p{
    color: #ff6f00;
    font-size: 0.28rem
  ;
  }
</style>



